import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { DataTableResource } from 'angular7-data-table';

import { AdsService } from './ads.service';

@Component({
  selector: 'app-ads',
  templateUrl: './ads.component.html',
  styleUrls: ['./ads.component.scss'],
})
export class AdsComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 5;
  limits = [5, 10, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  constructor(
    private adsService: AdsService,
    private modalService: BsModalService,
    private toastr: ToastrService
  ) {}

  ngOnInit(): void {
    this.adsService.getAll();

    this.adsService.ads.subscribe((data) => {
      if (data) {
        this.itemResource = new DataTableResource(data);
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
        this.itemResource.count().then((count) => (this.itemCount = count));
      }
    });
  }

  reloadItems(event) {
    if (this.itemResource) {
      this.itemResource.query(event).then((items) => (this.items = items));
    }
  }

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  deleteElement() {
    this.modalRef.hide();
    this.adsService.deleteOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.adsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
